  fill: #ddd;
  stroke: none;
}

#epiCurve .axis--x .domain,
#epiCurve .axis--grid .tick line {
  stroke: #fff;
}

#epiCurve .axis--grid .tick--minor line {
  stroke-opacity: 0.5;
}

